import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CForm,
  CButton,
  CFormLabel,
  CFormInput,
  CFormFeedback,
  CFormSelect,
  CButtonGroup,
} from "@coreui/react";

import { Authentification } from "../../components/index";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import { useHistory, useParams, Link } from "react-router-dom";

import Message from "../../components/Message";

import { getOptionLists, addOptionList } from "../../provider/optionListFetch";

const schemaUser = yup.object().shape({
  name: yup.string().required("name is required"),
  description: yup.string().required("description is required"),
  type: yup.string().required("type are required"),
  options: yup.array(),
  foods: yup.array(),
});

const AjoutOptionList = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  /** Hook Route */
  let history = useHistory();
  let { id } = useParams();
  const paramQuery = window.location.hash;
  const paramsJS = new URLSearchParams(
    paramQuery.substring(paramQuery.lastIndexOf("?"))
  );
  const foods = paramsJS.has("foods") ? paramsJS.get("foods") : [];
  const options = paramsJS.has("options") ? paramsJS.get("options") : [];

  console.log(
    "params",
    paramQuery.substring(paramQuery.lastIndexOf("?")),
    foods,
    options
  );

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
    setValue,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onTouched",
    defaultValues: { foods, options },
  });

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    if (id) {
      async function fetch() {
        const resp = await getOptionLists(null, id);

        if (resp?.status) {
          setValue("name", resp?.data.name);
          setValue("description", resp?.data.description);
          setValue("type", resp?.data.type);
          setValue("options", resp?.data.options);
          setValue("foods", resp?.data.foods);
        } else {
          setMessage(resp?.error);
        }
      }

      fetch();
    }
  }, [id, setValue]);

  const onSubmit = async (data) => {
    let resp = null;

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await addOptionList(data, id);
    }

    if (resp?.status) {
      /** Reset */
      resetField("name");
      resetField("description");
      resetField("type");
      resetField("options");
      resetField("foods");

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  const back = () => {
    history.push(`/ajout-food`);
  };

  return (
    <CRow>
      <Authentification />
      {error && <Message> {message}</Message>}
      {success && <Message> {`Success Ajout ou Edite`}</Message>}
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Ajout OptionList</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => back()}
                  >
                    Retour
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CForm
              className={`row g-3 needs-validation ${
                isValid && "was-validated"
              }`}
              onSubmit={handleSubmit(onSubmit)}
            >
              <CCol md={6}>
                <CFormLabel htmlFor="name">Name</CFormLabel>
                <CFormInput
                  type="text"
                  id="name"
                  required
                  {...register("name")}
                />
                <CFormFeedback invalid>{errors.name?.message}</CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="lastName">Description</CFormLabel>
                <CFormInput
                  type="text"
                  id="description"
                  required
                  {...register("description")}
                />
                <CFormFeedback invalid>
                  {errors.description?.message}
                </CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="type">Type</CFormLabel>
                <CFormSelect
                  aria-label="Type"
                  id="type"
                  required
                  {...register("type")}
                >
                  <option disabled>...</option>
                  <option value="multiple">multiple</option>
                  <option value="single">single</option>
                </CFormSelect>
                <CFormFeedback invalid>{errors.type?.message}</CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="options">Option</CFormLabel>
                <CFormSelect
                  size="lg"
                  multiple
                  aria-label="Options"
                  id="options"
                  {...register("options")}
                >
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </CFormSelect>
                <small className="form-text text-muted">
                  <Link to={`/ajout-option`}>Ajouter une option</Link>{" "}
                </small>
              </CCol>
              <CCol xs={12}>
                <CButton
                  color="primary"
                  type="submit"
                  className={isSubmitting ? "disabled" : " "}
                >
                  Submit
                </CButton>
              </CCol>
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};
export default AjoutOptionList;
